// Funo para habilitar edio do nome
function habilitarNome() {
    const nome = document.getElementById('confNome');
    
    if (nome.disabled) {
        nome.disabled = false;
        nome.style.color = 'white';
    } else {
        nome.disabled = true;
        nome.style.color = "gray";
        verificar_senha(); // Chama a funo para atualizar o usurio
    }
}

// Funo para habilitar edio do email
function habilitarEmail() {
    const email = document.getElementById('confEmail');
    
    if (email.disabled) {
        email.disabled = false;
        email.style.color = 'white';
    } else {
        email.disabled = true;
        email.style.color = "gray";
        verificar_senha(); // Chama a funo para atualizar o usurio
    }
}

// Funo para habilitar edio da senha
function habilitarSenha() {
    const senha = document.getElementById('confSenha');
    if (senha.disabled) {
        senha.disabled = false;
        senha.style.color = "white";
        senha.type = "text"
    } else {
        senha.disabled = true;
        senha.style.color = "gray";
        senha.type = "password"
        
        verificar_senha(); // Chama a funo para atualizar o usurio
    }
}

