// Funo para verificar o usurio
async function verificarUsuario() {
    const email = document.getElementById('logEmail')?.value;
    const senha = document.getElementById('logSenha')?.value;

    if (!email || !senha) {
        console.error('Preencha todos os campos');
        window.alert('Preencha todos os campos');
        return;
    }

    try {
        const response = await fetch('http://localhost:3000/verificar_login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ email, senha }),
        });

        const responseData = await response.json();

        if (response.ok) {
            const userData = {
                id_cadastro: responseData.id_cadastro, // Corrigido para id_cadastro
                nome: responseData.nome,
                email: responseData.email,
                senha: responseData.senha
            };

            localStorage.setItem('userData', JSON.stringify(userData));
            console.log('Usurio encontrado com sucesso!', userData);
            window.alert('USURIO EXISTENTE');
            window.location.href = "inicio-jogo.html";
        } else {
            console.error(responseData.mensagem);
            window.alert(responseData.mensagem);
        }
    } catch (error) {
        console.error('Erro na chamada de API:', error);
        window.alert('Erro na chamada de API');
    }
}

async function carregarInformacoes(userData) {
    try {
        console.log('Dados do usurio recebidos:', userData);

        if (userData && userData.nome && userData.email && userData.senha) {
            // Atribui os valores aos campos HTML
            document.getElementById('confNome').value = userData.nome;
            document.getElementById('confEmail').value = userData.email;
            document.getElementById('confSenha').value = userData.senha;
        } else {
            console.error('Dados do usurio incompletos:', userData);
        }
    } catch (error) {
        console.error('Erro ao carregar informaes do usurio:', error);
    }
}

function carregarDados() {
    try {
        const userDataString = localStorage.getItem('userData');
        if (userDataString) {
            const userData = JSON.parse(userDataString);
            carregarInformacoes(userData);
        } else {
            console.error('Dados do usurio no encontrados no armazenamento local');
        }
    } catch (error) {
        console.error('Erro ao carregar dados do usurio do armazenamento local:', error);
    }
}

// Funo para atualizar o usurio no servidor
async function atualizarUsuario(userData) { 
    // Verifica se userData contm id_cadastro
    if (!userData || !userData.id_cadastro) {
        console.error('Dados do usurio incompletos ou id_cadastro no encontrado');
        return;
    }

    try {
        console.log('Dados do usurio:', userData); // Verifica os dados do usurio obtidos do armazenamento local
        const id_cadastro = userData.id_cadastro;

        // Obter os valores dos campos
        const nome = document.getElementById('confNome').value;
        const email = document.getElementById('confEmail').value;
        const senha = document.getElementById('confSenha').value;

        // Verificar se todos os campos esto preenchidos
        if (!nome || !email || !senha) {
            console.error('Por favor, preencha todos os campos');
            window.alert('Por favor, preencha todos os campos');
            return;
        }

        const data = {
            id_cadastro: id_cadastro,
            nome: nome,
            email: email,
            senha: senha
        };

        console.log('Dados a serem enviados para atualizao:', data);

        const response = await fetch('http://localhost:3000/atualizar_usuario', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(data),
        });

        const responseData = await response.json();

        if (response.ok) {
            console.log('Usurio atualizado com sucesso!', responseData);
            window.alert('Usurio atualizado com sucesso!');
            localStorage.setItem('userData', JSON.stringify(data)); // Atualiza os dados do usurio no armazenamento local
        } else {
            console.error('Erro ao atualizar usurio:', responseData.error);
            window.alert(responseData.mensagem);
        }
    } catch (error) {
        console.error('Erro ao atualizar usurio:', error);
        window.alert('Erro ao atualizar usurio');
    }
}

async function verificar_senha() {
    const senhaDigitada = prompt("Digite sua senha:");

    if (!senhaDigitada) {
        console.error('Senha no fornecida');
        return;
    }

    try {
        const data = { senha: senhaDigitada };

        const response = await fetch('http://localhost:3000/verificar_senha', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(data),
        });

        const responseData = await response.json();

        if (response.ok) {
            if (responseData.senhaEncontrada) {
                console.log('Senha encontrada. Voc pode prosseguir com a atualizao.');

                const userDataString = localStorage.getItem('userData');
                if (!userDataString) {
                    console.error('Dados do usurio no encontrados no armazenamento local');
                    return;
                }

                const userData = JSON.parse(userDataString);
                atualizarUsuario(userData);
            } else {
                console.error('Senha no encontrada. Por favor, verifique a senha digitada.');
                window.alert('Senha no encontrada. Por favor, verifique a senha digitada.');
            }
        } else {
            console.error('Erro ao verificar senha:', responseData.error);
            window.alert('Erro ao verificar senha. Por favor, tente novamente.');
        }
    } catch (error) {
        console.error('Erro ao verificar senha:', error);
        window.alert('Erro ao verificar senha. Por favor, tente novamente.');
    }
}

async function excluirConta() {
    const id_cadastro = JSON.parse(localStorage.getItem('userData'))?.id_cadastro;

    if (!id_cadastro) {
        console.error('ID de cadastro no encontrado');
        return;
    }

    if (confirm("Deseja realmente excluir sua conta?")) {
        if (prompt('Para confirmar a excluso, escreva: Excluir minha conta') === 'Excluir minha conta') {
            try {
                const response = await fetch('http://localhost:3000/apagar_usuario', {
                    method: 'DELETE',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({ id_cadastro: id_cadastro })
                });

                if (response.ok) {
                    alert("Conta excluda com sucesso!");
                    localStorage.removeItem('userData');
                    window.location.href = "login.html";
                } else {
                    throw new Error('Erro ao excluir a conta');
                }
            } catch (error) {
                alert("Erro ao excluir a conta: " + error.message);
            }
        } else {
            alert("Operao cancelada!");
        }
    }
}

function teste() {
    let deleteButton = document.getElementById("DeleteConta");
    deleteButton.addEventListener('click', excluirConta);
};